.Button {
  border-style: none;
  outline-style: none;
  padding: 15px 40px;
  outline: none !important;

  height: 45px;

  background: #11782e;
  box-shadow: 0px 8px 12px rgba(19, 21, 20, 0.08);
  border-radius: 8px;
  color: white;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
  outline: none;
  -webkit-font-smoothing: subpixel-antialiased;
}

.Button:hover {
  cursor: pointer;
}

.Button--disabled {
  background-color: grey;
  cursor: not-allowed !important;
}

.HowWorks {
  display: flex;
  justify-content: space-between;
  margin: auto;
  margin-bottom: 20px;
  width: 100%;
}

.HowWorks__title {
  font-family: "DM Serif Text";
  font-size: 20px;
  color: #181818;
  margin: 0px auto 20px auto;
}

.HowWorks__step {
  display: flex;
  align-items: center;
  margin-right: 10px;
}

.HowWorks__step__number {
  font-family: "DM Serif Text";
  font-size: 48px;
  color: #397e47;
  margin-left: 10px;
  margin-right: 10px;
}

.HowWorks__step__description {
  font-family: "Open Sans";
  color: #141414;
  font-size: 14px;
  font-weight: 600;
  text-align: left;
  line-height: 1.3em;
}

@media screen and (max-width: 767px) {
  .HowWorks,
  .HowWorks__title {
    display: block;
    max-width: calc(100% - 40px);
    text-align: center;
  }
  .HowWorks__title {
    margin-top: 30px;
    margin-bottom: 20px;
  }
